import React, { useState } from 'react'
//eslint-disable-next-line
import Emoji from './Emoji'

import './App.css';

function App() {
  const [input, setInput] = useState('')
  const [isGeorgia, setIsGeorgia] = useState(true)

  const [submited, setSubmited] = useState(false)

  const [isValid, setIsValid] = useState(false)

  function geTelephoneCheck(str) {
    const geValidator = /^(995\s?)?(\d{3}[\-\s]?(\d{2}[\-\s]?\d{2}[\-\s]?\d{2}$|\d{3}[\-\s]?\d{3}$))/;
    return geValidator.test(str);
  }

  function usTelephoneCheck(str) {
    const usValidator = /^(1\s?)?(\(\d{3}\)|\d{3})[\-\s]?\d{3}[\-\s]?\d{4}$/;
    return usValidator.test(str);   
  }

  const handleChange = e => {
    setInput(e.target.value)
  }

  const handleButton = e => {
    setSubmited(true)
    if (isGeorgia === true) {
      if(geTelephoneCheck(input) === true) {
        setIsValid(true)
        setTimeout(() => {
          setSubmited(false)
          setIsValid(false)
        }, 1000)
      }
    }else {
      if(usTelephoneCheck(input) === true) {
        setIsValid(true)
        setTimeout(() => {
          setSubmited(false)
          setIsValid(false)
        }, 1000)
      }
    }
    setTimeout(() => {
      setSubmited(false)
      setIsValid(false)
    }, 1000)
  }

  return (
    <div id="App">
      {/* <h1 id="title">Telephone Number Validator</h1> */}

      <label>Country</label>
      <select onChange={() => {
        setIsGeorgia(!isGeorgia) 
        setIsValid(false)
      }} className="input" id="country">
        <option>+995 Georgia</option>
        <option>+1 US</option>
      </select>

      <br />

      <label>Phone Number</label>
      <input className="input" type="text" value={input} onChange={handleChange}/> 
      <label style={{fontSize: '12.5px'}}>
        {isGeorgia === true
      ? `Example number (Georgian): 555 55 55 55`
      : `Example number (US): 555-555-1212`
        }
      </label>

      <br/>
      <span>
        <button onClick={handleButton} className="btn-select">Validate</button>
        <button onClick={() => {
          setSubmited(false)
          setInput('')
          setIsValid(false)
        }}>Reset</button>
      </span>

      {
        submited ?
        isValid === true
      ? <span style={{color: 'cyan'}}> <br/> Valid Telephone Number</span> 
      : <span style={{color: 'red'}}> <br/> Invalid Telephone Number</span>   
        : null
      }
      <br />

    </div>
  );
}

export default App;
